import React from "react";

//component
import { Container, Row, Col, Image, Badge } from "react-bootstrap";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faClock, faLocationPin } from "@fortawesome/free-solid-svg-icons";
import { isMobile, isTablet } from "react-device-detect";
import { v4 as uuidv4 } from "uuid";

export default function CardsVisit({ data, onSubmit }) {
  const mapNewTab = (e, link) => {
    switch (e.button) {
      case 0:
      case 1:
        window.open(link);
        break;
      default:
    }
  };

  return (
    <>
      <Container
        fluid
        className={`m-0 p-0 ${
          isMobile && !isTablet ? "h-100" : "h-50"
        } my-5 d-flex justify-content-center`}
      >
        <Container className={`visit-box-shadow m-0 p-0 h-100`}>
          <Row className="m-0 p-0 w-100 h-100 justify-content-center">
            <Col
              lg={6}
              xs={{ span: 12 }}
              md={6}
              className={`${isMobile && !isTablet ? "h-50 order-2" : ""}`}
            >
              <Row className="m-0 p-0 w-100 h-100">
                <Col lg={12} className="m-0 p-0">
                  <iframe
                    title="wtbs google map"
                    className="google-map"
                    src={data.content_url}
                    style={{ border: 0 }}
                    allowFullScreen={true}
                    loading="lazy"
                    referrerPolicy="no-referrer-when-downgrade"
                  ></iframe>
                </Col>
              </Row>
            </Col>
            <Col
              lg={5}
              md={6}
              xs={{ span: 12 }}
              className={`${isMobile ? "h-50  order-1" : "m-0 p-0"}`}
            >
              <Row className="m-0 p-0 w-100 h-100">
                <Col lg={12} className="m-0 p-0 d-flex align-items-center">
                  <Row className="m-0 p-0 w-100">
                    <Col
                      lg={4}
                      xs={3}
                      className={`${isMobile ? "p-0 m-0" : ""}`}
                    >
                      <Image
                        fluid
                        className={`h-100`}
                        src={
                          process.env.REACT_APP_API_URL +
                          "/images/contents/" +
                          data.content_thumbnail
                        }
                      />
                    </Col>
                    <Col
                      lg={8}
                      xs={9}
                      id={`${isMobile ? "" : "visit-header-title-container"}`}
                      className={`d-flex align-items-center`}
                    >
                      <h1
                        id={`${
                          isMobile
                            ? "visit-header-title-small"
                            : "visit-header-title"
                        }`}
                        className={`px-lg-3`}
                        onMouseDown={(e) => mapNewTab(e, data.content_url)}
                      >
                        {data.content_title}
                      </h1>
                    </Col>
                  </Row>
                </Col>
                <Col lg={12} className="m-0 p-0">
                  <p className="visit-date px-lg-3">
                    <FontAwesomeIcon icon={faClock}></FontAwesomeIcon>{" "}
                    {data.content_start_time} - {data.content_end_time}
                  </p>
                  <p className="visit-address px-lg-3">
                    <FontAwesomeIcon icon={faLocationPin}></FontAwesomeIcon>{" "}
                    {data.content_location}
                  </p>
                </Col>
                <Col lg={12} className="m-0 p-0">
                  {data.content_tags?.map((tags) => {
                    return (
                      <Badge
                        className="pill-cursor m-1"
                        bg="primary"
                        pill
                        key={uuidv4()}
                        onClick={() => onSubmit({ Search: tags })}
                      >
                        {tags}
                      </Badge>
                    );
                  })}
                </Col>
              </Row>
            </Col>
          </Row>
        </Container>
      </Container>
    </>
  );
}
